.hicon {
	padding-top: 3em;
	background-position: top center;
	background-repeat: no-repeat;
	background-size: 2em;
	&.hicon-clock {
		background-image: url(../imgs/icon-clock.svg);
	}
	&.hicon-speech {
		background-image: url(../imgs/icon-speech.svg);
	}
	&.hicon-listening {
		background-image: url(../imgs/icon-listening.svg);
	}
	&.hicon-conversation {
		background-image: url(../imgs/icon-conversation.svg);
	}
	&.hicon-understanding {
		background-image: url(../imgs/icon-understanding.svg);
	}
}
h2.hicon { margin-top: 2em; }

@media all and (min-width: $mediumWidth) {
	.hicon {
		padding-top: 0;
		min-height: 3em;
		background-position: top left;
		background-size: 3em;
	}
}