@import 'globals/variables';
@import 'globals/normalize';
@import 'globals/typography';
@import 'partials/forms';
@import 'partials/icons';

* { box-sizing: border-box; }
html { font-size: 62.5%; }

body {
	background-color: #efefef;
}

header[role=banner] {
	padding: 1rem 1.5rem;
	background-color: #fff;
	border-bottom: 1px solid #999;
	#logo {
		width: 134px;
	}
}
section[role=main] {
	margin: 2rem 1rem;
}
section[role=main] > div {
	margin-bottom: 2rem;
	padding: 1.5rem;
	text-align: center;
	background-color: #fff;
	box-shadow: 0 0 10px rgba(0,0,0,.25);
	border-radius: 5px;
}

footer[role=contentinfo] {
	padding: 1rem;
	font-size: 1.4rem;
	text-align: center;
	color: #999;
	background-color: #fff;
	border-top: 1px solid #999;

	ul {
		margin: 0;
		padding: 0;
		list-style: none;
		> li {
			display: inline-block;
			margin: 0 1em;
		}
		a {
			display: inline-block;
			padding: .5em;
			color: #999;
		}
	}
}